import React from "react";
import './styles/style.css'
import Items from "./components/Item";
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';
import AddTaskIcon from '@mui/icons-material/AddTask';
import Tooltip from '@mui/material/Tooltip';
import { useState } from "react";

const App=()=>{
  document.title = "Todo List App"

  const [inputVal, setinputVal] = useState("")

  const [taskArray,setTaskArray] = useState([])

  const handleEvent=(e)=>{setinputVal(e.target.value)}

  const addItem=()=>{
    if(inputVal!==""){
    setTaskArray((prevItem)=>{
      return [...prevItem,inputVal]
    })
    setinputVal("");
  }
  }

  const delItem=(id)=>{
    setTaskArray((prevItem)=>{
      return [...prevItem.filter((val,ind)=>{
        return  id!==ind;  
      })]
    })
  }


  return(
    <>
      <div className="whole-box">
        <div className="card">
          <div className="input-button">
          <TextField required className="input" label="Add an Item" value={inputVal} onChange={handleEvent}/>
          <Tooltip title="Add Task" arrow>
          <Button onClick={addItem} className="button-add" variant="contained"><AddTaskIcon/></Button>
          </Tooltip>
          </div>
          <hr />

          <div className="all-tasks">
              {taskArray.map((val,index)=>{
                return(
                  <Items
                  key = {index}
                  task = {val}
                  del = {delItem}
                  id = {index}
                  />
                )
              })
              }
          </div>
        </div>
      </div>
    </>
  )
}

export default App;