import React, { useState } from "react";
import DeleteIcon from '@mui/icons-material/Delete';
import Button from '@mui/material/Button';
import Tooltip from '@mui/material/Tooltip';
const Items=(props)=>{
    const [line, setline] = useState(false);

    const cutTask=()=>{
        setline(true);
    }

    return(
        <>
            <div className="task">
                <Tooltip title="Delete Task" arrow>
                <Button onClick={cutTask} onDoubleClick={()=>{props.del(props.id)}} className="button-delete" variant="contained">
                  <DeleteIcon/>
                </Button>
                </Tooltip>
                <p id={props.id} style={line===true?{textDecoration:"line-through",opacity:"0.4"}:{textDecoration:"none",opacity:"0.6"}}>{props.task}</p>
              </div> 
        </>
    )
}

export default Items;